.featured {
    height: 100vh;
    width: 100%;
}

.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 100px;

}

.featured--name {
    font-size: 60px;
    font-weight: bold;
}

.featured--info {
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.featured--points, 
.featured--year, 
.featured--seasons {
    display: inline-block;
    margin-right: 15px;
}

.featured--points{
    color: #46d369; 
}

.featured--overview {
    margin-top: 10px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.featured--buttons a {
    margin-top: 15px;
}

.featured--watch, 
.featured--list {
    display: inline-block;
    font-size: 15px;
    font-weight: bold;
    text-decoration: none;
    text-transform:uppercase ;
    margin-right: 10px;
    padding: 10px 20px;
    border-radius: 5px;
    opacity: 1;
    transition: all ease 0.2s;
}

.featured--watch:hover, 
.featured--list:hover {
    opacity: 0.7;
}

.featured--watch {
    background-color: #fff;
    color: #000;
}

.featured--list {
    background-color: #333;
    color: #fff;
}

.featured--genres {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width: 760px) {
    .featured {
        height: 90vh;
    }
    .featured--name {
        font-size: 40px;
    }
    .featured--info {
        font-size: 16px;
    }

    .featured--overview {
        font-size: 14px;
        max-width: 100%;
        margin-right: 35px;
    }

    .featured--watch,
    .featured--list {
        font-size: 16px;
    }

    .featured--genres {
        font-size: 14px;
    }
}